nav {
  padding-left: 15px;
  padding-right: 15px;
  background: rgb(92, 203, 255);
  margin-top: 15px;
  width: 250px;
  height: 80vh;
  ul{
    padding: 0;
    margin: 0;
    list-style: none;
    li{
      padding: 10px 0;
      a{
        color: #fff;
        text-decoration: none;
        font-family: Arial, sans-serif;
        text-transform: uppercase;
        transition: color 0.2s ease-in-out;
        
        
      }

      .active{
        color: rgb(1, 132, 255);
      }
    }
  }
}
